body {
    counter-reset: project;
}

.project-name::before {
    counter-increment: project;
    content: counter(project) ". ";
}

.project:not(:first-child) {
    margin-top: 8px;
}

.certificate:not(:first-child) {
    margin-top: 8px;
}

.certification {
